
$main: #002DAA;
$main2: #080E52;

.header {
    width: 100%;
    height: 60px;
    background: linear-gradient(to right, $main, $main2);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 900;
    font-size: 20px;
    text-transform: uppercase;
}

.page-content {
    box-sizing: border-box;
    padding: 20px;
    min-height: calc(100vh - 120px)
}

.footer {
    width: 100%;
    height: 60px;
    background: linear-gradient(to right, $main, $main2);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 900;
    font-size: 20px;
    text-transform: uppercase;
}